import React from 'react';
import './Login.css';
import firebase from 'firebase/app';
require('firebase/auth');

class Login extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            name: '',
            email: '',
            password: '',
            confirmPassword: '',
            chatPassword: '',
            chat: ''
        }
    }

    writeName = ({target: {value}}) => {
        this.setState({name: value.replace(/[^a-zA-Z0-9а-яА-Я]/g, '')});
    }

    writeEmail = ({target: {value}}) => {
        this.setState({email: value.replace(/[^a-zA-Z0-9@.]/g, '')});
    }
    writePassword = ({target: {value, id}}) => {
        this.setState({[id]: value.replace(/[^a-zA-Z0-9]/g, '')});
    }

    validate = (name, email, password, confirmPassword) => {
        if(name === '' && email === '' && password === '' && confirmPassword === '') {
            return false;
        }
        else if(password !== confirmPassword || password.length < 6) {
            return false;
        }
        else {
            return true;
        }
    }

    singUp = () => {
        this.setState({chat: 'GENERAL1'});
        let inputs = document.querySelectorAll('.input_form_sign');
        document.querySelectorAll('.ul_tabs > li')[0].className=""; 
        document.querySelectorAll('.ul_tabs > li')[1].className="active"; 
          
        for(var i = 0; i < inputs.length; i++) {
            if(i !== 1 || i !== 2){
                document.querySelectorAll('.input_form_sign')[i].className = "input_form_sign d_block";
            }
        } 
        
        setTimeout(function(){
            for(var d = 0; d < inputs.length; d++) {
                if(d !== 4 && d !== 5){
                    document.querySelectorAll('.input_form_sign')[d].className = "input_form_sign d_block active_inp";  
                }
                else {
                    document.querySelectorAll('.input_form_sign')[d].className = "input_form_sign d_block"
                }
            }
        },100);
        document.querySelector('.s-in').classList.add('none');   
        document.querySelector('.s-up').classList.remove('none'); 
    }

    signIn = () => {
        let inputs = document.querySelectorAll('.input_form_sign');
        document.querySelectorAll('.ul_tabs > li')[0].className = "active"; 
        document.querySelectorAll('.ul_tabs > li')[1].className = ""; 
          
        for(var i = 0; i < inputs.length; i++) {
            switch(i) {
                case 1: break;
                case 2: break;
                default: 
                document.querySelectorAll('.input_form_sign')[4].value = 'GENERAL1'
                document.querySelectorAll('.input_form_sign')[i].className = "input_form_sign d_block";
            }
        } 
        
        setTimeout(function(){
            for(var d = 0; d < inputs.length ; d++  ) {
                switch(d) {
                    case 1: break;
                    case 2: break;
                    default:
                    document.querySelectorAll('.input_form_sign')[d].className = "input_form_sign d_block";  
                    document.querySelectorAll('.input_form_sign')[2].className = "input_form_sign d_block active_inp";  
                    document.querySelectorAll('.input_form_sign')[4].className = "input_form_sign d_block active_select"
                }
            }   
        },100);
        document.querySelector('.s-up').classList.add('none');    
        document.querySelector('.s-in').classList.remove('none');  
    }

    signUpAccount = () => {
        if(this.validate(this.state.name, this.state.email, this.state.password, this.state.confirmPassword)) {
            firebase.auth().createUserWithEmailAndPassword(this.state.email, this.state.password)
            .then(dataUser => {
                dataUser.user.updateProfile({
                    photoURL: 'https://i.postimg.cc/PqxQPJ8h/iconfinder-Male-mongolian-skin-03-7724091.png',
                    displayName: this.state.name,
                });
                alert('Аккаунт зареєстрований');
            });
        }
        else {
            alert('err')
        }

    }

    signInAccount = () => {
        this.props.chatSelection(this.state.chat);
        if(this.state.email === '') {
            alert('Електронна адреса не може бути порожня');
        }
        else if(this.state.password === '') {
            alert('Пароль не може бути порожнім');
        }
        else if(document.querySelector('#chatPassword').classList.contains('active_inp') && this.state.chatPassword === '') {
            alert('Пароль до чату не може бути порожнім');
        }
        else if(document.querySelector('#chatPassword').classList.contains('active_inp') && this.state.chatPassword !== '000000') {
            alert('Пароль до чату неправильний');
        }
        else {
            firebase.auth().signInWithEmailAndPassword(this.state.email, this.state.password)
            .then(user => {
                this.props.user(true);
                console.log(user);
            })
            .catch((err) => {
                if(err.code === 'auth/wrong-password') {
                    alert('Неправильний пароль');
                }
                else if(err.code === 'auth/user-not-found') {
                    alert('Користувача з такою алектронною адресою не знайдено');
                }
                else {
                    alert('Помилка');
                }
            });
        }
    }

    chatSelection = (event) => {
        const {value} = event.target;
        this.setState({chat: value});
        if(value === 'PRIVATE1') {
            document.querySelector('#chatPassword').classList.add('active_inp');
        }
        else {
            document.querySelector('#chatPassword').classList.remove('active_inp');
        }
    }

    // componentDidMount() {
    //     console.log(this.language());
    // }

    render() {
        return (
            <div className="cont_principal">
                <div className="cont_centrar">
                    <div className="cont_login">
                        <div className="cont_tabs_login">
                            <ul className='ul_tabs'>
                                <li className="active"><p onClick={this.signIn}>SIGN IN</p>
                                    <span className="linea_bajo_nom"></span>
                                </li>
                                <li><p onClick={this.singUp}>SIGN UP</p>
                                    <span className="linea_bajo_nom"></span>
                                </li>
                            </ul>
                        </div>
                        <div className="cont_text_inputs">
                            <input 
                                type="text" 
                                className="input_form_sign" 
                                id="name"
                                placeholder="NAME"
                                onInput={this.writeName}
                                value={this.state.name} />
                            <input 
                                type="text" 
                                className="input_form_sign d_block active_inp" 
                                id="email"
                                placeholder="EMAIL"
                                onInput={this.writeEmail}
                                value={this.state.email} />
                            <input 
                                type="password" 
                                className="input_form_sign d_block active_inp" 
                                id="password"
                                placeholder="PASSWORD"
                                onInput={this.writePassword} 
                                value={this.state.password} />  
                            <input 
                                type="password" 
                                className="input_form_sign"
                                id="confirmPassword" 
                                placeholder="CONFIRM PASSWORD"
                                onInput={this.writePassword} 
                                value={this.state.confirmPassword} />
                            <select className="input_form_sign d_block active_select" onChange={this.chatSelection}>
                                <option value="GENERAL1" defaultValue>GENERAL1</option>
                                <option value="GENERAL2">GENERAL2</option>
                                <option value="PRIVATE1">PRIVATE1</option>
                            </select>
                            <input 
                                type="password" 
                                className="input_form_sign d_block"
                                id="chatPassword" 
                                placeholder="CHAT PASSWORD"
                                onInput={this.writePassword} 
                                value={this.state.chatPassword} />
                        </div>
                        <div>
                            <button className="btn_sign s-in" onClick={this.signInAccount}>SIGN IN</button>   
                            <button className="btn_sign s-up none" onClick={this.signUpAccount}>SIGN UP</button>   
                        </div>   
                    </div>
                </div>
            </div>
        );
    }
}

export default Login;