import React, { useEffect } from 'react';
import './Chat.css';
import {auth} from '../index';
import {icon} from '../utils/icon';
import {useCollectionData} from "react-firebase-hooks/firestore";
import firebase from 'firebase/app';
import 'firebase/firestore';

const Chat = (chat) => {
  let text = '';
  let iconUrl = auth.currentUser.photoURL;
  const [messages, loading] = useCollectionData(
    firebase.firestore().collection(chat.chat).orderBy('createdAt')
  )

  const sendMessage = async () => {
    if(text !== '') {
      firebase.firestore().collection(chat.chat).add({
        uid: auth.currentUser.uid,
        name: auth.currentUser.displayName,
        photoURL: iconUrl,
        data: formatDate(new Date()),
        text: text,
        createdAt: firebase.firestore.FieldValue.serverTimestamp()
      });
      document.querySelector('.msger-chat').scrollTop = document.querySelector('.msger-chat').scrollHeight;
      document.querySelector('.msger-input').value = '';
    }
  }

  const handleKeyPress = event => {
    if (event.key === 'Enter') {
      sendMessage();
    }
  };

  const formatDate = (date) => {
    const h = "0" + date.getHours();
    const m = "0" + date.getMinutes();

    return `${h.slice(-2)}:${m.slice(-2)}`;
  }

  const writeEmailAndPassword = (event) => {
    text = event.target.value;
  }
  
  const choiceIcon = (event) => {
    const imgs = document.querySelectorAll('.dropdown-content img');
    imgs.forEach(item => {
      item.classList.remove('icon-active')
    })
    event.target.classList.add('icon-active');
    
    auth.currentUser.updateProfile({
      photoURL: event.target.src
    })
    .then(() => {
      iconUrl = event.target.src;
    })
  }

  useEffect(() => {
    const imgs = document.querySelectorAll('.dropdown-content img');
    imgs.forEach(item => {
      // console.log(item.src);
      if(item.src === iconUrl) {
        item.classList.add('icon-active');
      }
    });
  })
  if (loading) {
    return <div></div>
  }
  return (
    <div className="msger">
      <div className="msger-header" style={{display: 'flex'}}>
        <div className="dropdown">
          <button className="dropbtn">Icon</button>
          <div className="dropdown-content" onClick={choiceIcon}>
            <img src={icon['icon-male_1']} alt="" />
            <img src={icon['icon-male_2']} alt="" />
            <img src={icon['icon-male_3']} alt="" />
            <img src={icon['icon-male_4']} alt="" />
            <img src={icon['icon-male_5']} alt="" />
            <img src={icon['icon-male_6']} alt="" />
            <img src={icon['icon-female_1']} alt="" />
            <img src={icon['icon-female_2']} alt="" />
            <img src={icon['icon-female_3']} alt="" />
          </div>
        </div>
        <p className="mshger-info">name: {auth.currentUser.displayName} <span style={{marginLeft: '20px'}}>chat: {chat.chat}</span></p>
      </div>
    
      <div className="msger-chat">
        {messages.map(message =>
          <div className="mar-btn">
            <div className={auth.currentUser.uid === message.uid ? "media-right" : "media-left"}>
              <img src={message.photoURL} className="img-circle img-sm" alt=""/>
            </div>
            <div className={auth.currentUser.uid === message.uid ? "media-body pad-hor speech-right" : "media-body pad-hor"}>
              <div className="speech">
                <a className="media-heading" href="/" onClick={e => e.preventDefault()}>{message.name}</a>
                <p className="message-text">{message.text}</p>
                <p className="speech-time">
                  <i className="fa fa-clock-o fa-fw"></i>{message.data}     
                </p>
              </div>
            </div>
          </div>
        )}
      </div>
    
      <div className="msger-inputarea">
        <input type="text" className="msger-input" placeholder="Enter your message..." onInput={writeEmailAndPassword} onKeyPress={handleKeyPress}/>
        <button type="submit" className="msger-send-btn" onClick={sendMessage}>Send</button>
      </div>
    </div>
  );
};

export default Chat;