import React from 'react';
import {Route, Switch, Redirect} from 'react-router-dom'
import {CHAT_ROUTE, LOGIN_ROUTE} from "../utils/consts";
import Login from "../login/Login";
import Chat from "../chat/Chat";

class AppRouter extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            user: false,
            chat: '',
        }
    }

    user = (status) => {
        this.setState({user: status});
    }

    chatSelection = async (chat) => {
        if(!!!chat) {
            this.setState({chat: 'GENERAL1'})
        }
        else {
            this.setState({chat: chat})
        }
    }

    render() {
            return this.state.user && !!this.state.chat ?
            (
                <Switch>
                    <Route path="/chat" render={() => <Chat chat={this.state.chat} />} exact={true}/>
                    <Redirect to={CHAT_ROUTE}/>
                </Switch>
            )
            :
            (
                <Switch>
                    <Route path="/login" render={() => <Login user = {this.user} chatSelection = {this.chatSelection} />} exact={true}/>
                    <Redirect to={LOGIN_ROUTE}/>
                </Switch>
            )
        };
    }
   

export default AppRouter;